html, body {
    margin:0;
    padding:0;
    background-color: black;
}
.containerForm {
    background-color: black;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: fantasy;
    height: 100%;
    width: 100vw;
}

.borderProperty {
    border-width: 2px;
    border-style: solid;
    border-color: white;
    border-radius: 2rem;
    padding: 100px;
    margin-bottom: 25px;
}
.passport {
    display: flex;
    flex-direction: column;
}
.inputVal {
    width: 410px;
    height: 60px;
    color: white;
    background-color: black;
    border-style: solid;
    border-color: white;
    border-width: 1.5px;
    border-radius: 1rem;
    font-size: large;
    font-family: fantasy;
}
.radioOption {
    width: 25px;
    height: 25px;
}
.gender {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
label {
    background-image: linear-gradient(#93fb9d, #09c7fb);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 30px;
    font-weight: bolder;
}

.submitBtn {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 35px;
    height: 65px;
    width: 140px;
    font-size: 22px;
    font-family: fantasy;
    background-image: linear-gradient(#93fb9d, #09c7fb);
    border: none;
    border-radius: 1rem;
    font-weight: bolder;
    padding-top: 2px;
}
button:hover {
    transform: scale(1.1);
    transition:ease-in;
    transition-duration: 0.3s;
}
.genderVal {
    font-size: 20px;
}
.heading {
    font-size: 42px;
}

.submitBtn:disabled {
    background-image: linear-gradient(grey, grey);
    color: white;
    cursor: not-allowed;
}

